@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");
*, *::before, *::after {
  padding: 0;
  margin: 0;
}

body {
  width: 100vw;
  display: flex;
  justify-content: center;
  font-family: "Roboto", sans-serif;
  font-style: normal;
}

.container {
  background-color: #ffffff;
  width: 1440px;
  padding-top: 96px;
  display: flex;
  justify-content: center;
}
.container .module {
  display: flex;
  justify-content: center;
  gap: 96px;
  flex-wrap: wrap;
}
.container .module .main__image {
  width: 505px;
  height: 330px;
  background-image: url("../../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_c_scale,w_784.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}
.container .module .aside {
  width: 380px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.container .module .aside__header--title {
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  color: #121212;
}
.container .module .aside__content {
  margin: 24px 0 32px 0;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  color: #121212;
}
.container .module .aside__button--popup {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 128px;
  height: 48px;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  color: #ffffff;
  background: #E4C1AE;
  border-radius: 24px 24px;
}

@media screen and (max-width: 1234px) {
  .container .module {
    gap: 64px;
  }
  .container .module .main__image {
    width: 80vw;
    height: 53.3333333333vw;
    background-image: url("../../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_c_scale,w_1234.jpg");
  }
  .container .module .aside {
    width: 80vw;
  }
}
@media screen and (max-width: 1002px) {
  .container {
    padding-top: 64px;
  }
  .container .module {
    gap: 64px;
  }
  .container .module .main__image {
    width: 80vw;
    height: 53.3333333333vw;
    background-image: url("../../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_c_scale,w_1002.jpg");
  }
  .container .module .aside {
    width: 80vw;
  }
  .container .module .aside__header--title {
    font-size: 32px;
  }
}
@media screen and (max-width: 784px) {
  .container {
    padding-top: 48px;
  }
  .container .module {
    gap: 32px;
  }
  .container .module .main__image {
    width: 85vw;
    height: 56.6666666667vw;
    background-image: url("../../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_c_scale,w_784.jpg");
  }
  .container .module .aside {
    width: 85vw;
  }
  .container .module .aside__header--title {
    font-size: 28px;
  }
}
@media screen and (max-width: 480px) {
  .container {
    padding-top: 16px;
  }
  .container .module {
    gap: 24px;
  }
  .container .module .main__image {
    width: calc(100vw - 16px);
    height: calc((100vw - 16px) / 1.5);
    background-image: url("../../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_c_scale,w_480.jpg");
  }
  .container .module .aside {
    width: calc(100vw - 16px);
  }
  .container .module .aside__header--title {
    font-size: 24px;
  }
}